import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import image from '../../assets/images/white-image.png';
import { Container } from 'semantic-ui-react';
import BlogPost from './sections/blog/BlogPost.jsx';

const POSTS = [
  {
    title: 'A szépség állati jó',
    date: '2017.06.12.',
    image: image,
    paragraphs: ['Facere assumenda commodi perspiciatis iure possimus in ut explicabo accusantium, praesentium itaque. Soluta quasi beatae quidem tempora odit eaque suscipit, recusandae expedita!',
      'Assumenda, minima, aperiam eos officia facilis modi est sit voluptatum tempore aliquid incidunt ea earum officiis quia. Quod ea, distinctio tenetur iusto.Vitae, harum. Est, architecto.']
  },
  {
    title: 'Ízletes karácsonyi receptek - 30 percben',
    date: '2017.03.01.',
    image: image,
    paragraphs: ['Sed eu hendrerit libero. Nulla pharetra erat non ex tempor, eget fermentum metus condimentum. Proin posuere sed lorem egestas facilisis. Nunc id interdum tortor.',
      'Proin posuere, consectetur adipisicing elit. Iure quasi debitis fugiat. Quod odio natus nemo quidem in pariatur laboriosam reprehenderit repudiandae perferendis quo! Voluptate!',
      'Laboriosam reprehenderit amet, consectetur adipisicing elit. Vitae, harum. Est, architecto.']
  },
  {
    title: 'Ajándéktippek valentin napra',
    date: '2017.01.20.',
    image: image,
    paragraphs: ['Lorem ipsum dolor sit amet, consectetur adipisicing elit. Facere assumenda commodi perspiciatis iure possimus in ut explicabo accusantium, praesentium itaque. Soluta quasi beatae quidem tempora odit eaque suscipit, recusandae expedita!',
      'Accusantium dolorum fuga nulla maiores est similique ut nemo repellendus omnis quaerat culpa iste deleniti eveniet blanditiis nam ab alias, unde. Dolorem ex vitae incidunt odio.consectetur adipisicing elit. Vitae, harum. Est, architecto.']
  }
];

export default class Blog extends React.Component {
  render() {
    const posts = [];

    for (let i = 0; i < POSTS.length; i++) {
      posts.push(<BlogPost key={i} {...POSTS[i]}/>);
    }

    return <div id='blog'>
             <section>
               <Container>
                 { posts }
               </Container>
             </section>
           </div>
  }
}



